<template>
  <div class="player">
    <div class="playerheader clearfix">
      <div class="logo">
        <img :src=login_logo  alt="千千音乐">
      </div>
      <div class="login">
        <span>登录/注册</span>
      </div>
      <div class="search">
        <input type="text" placeholder="请输入歌名、歌词、歌手或专辑">
        <i class="iconfont aliiconsearch"></i>
      </div> 
    </div>
    <div class="player_content clearfix">
      <div class="player_nav">
        <ul class="handlenav">
          <li v-for="(item,index) in playnav" :class="currentnav == index? 'currentnav':'' " :key ='index' @click="changenav(index)">{{item}}</li>
          <div class="menu" @click="changemenu">
            <i class="iconfont aliiconmenu"></i>
            <span id="moreclick">批量操作</span>
          </div>
        </ul>
        <!--  -->
        <!-- <div style="weight:100%;height:20px; margin-top:50px"></div>
        <quill-editor v-model="goods_introduce"></quill-editor> -->
      </div>
      <div class="geci">
        <div class="cover">
          <img :src="cover" alt="">
        </div>
        <div class="liric">
          <ul>
            <li>四季春秋</li>
            <li>作词：张敬豪</li>
            <li>演唱：耀乐团</li>
            <li>演唱：耀乐团</li>
            <li>相遇的那年 春天 细雨绵绵</li>
            <li>小桥流水人家身在江南的小街</li>
            <li>你出现 那闪耀的光线 迷我的眼</li>
          </ul>
        </div>
       
      </div>
    </div>
   
  </div>
</template>
<script>
export default {
  data() {
    return {
      login_logo: require('../../assets/logo.jpg'),
      cover: require('../../assets/songsheet/1.jpg'),
      playnav : ['播放列表','我的收藏','自建歌单','已购歌曲'],
      currentnav: 0,
      goods_introduce:''
    }
  },
  created () {
    window.addEventListener('beforeunload', () => {
      sessionStorage.setItem('currentnav', this.currentnav)
      
      
    })
    this.currentnav = window.sessionStorage.getItem('currentnav')
    
  },
  beforeDestroy() {
    sessionStorage.setItem('currentnav', this.currentnav)
    
  },
  methods:{
    changenav(id) {
      this.currentnav =id
    },
    changemenu(e) {
      let node_c = document.getElementById('moreclick')
      if(node_c.innerText == '批量操作') {
        node_c.innerText = '取消操作'
      } else {
        node_c.innerText = '批量操作'
      }
    }
  }
  
}
</script>
<style lang="scss">
@import '@/assets/css/player.scss'
</style>