import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../components/index.vue'
import Home from '../components/pages/home.vue'
import Songsheet from '../components/pages/songsheet.vue'
import Singer from '../components/pages/singer.vue'
import Myvip from '../components/pages/myvip.vue'
import Songbox from '../components/pages/songbox.vue'
import Myapp from '../components/pages/myapp.vue'
import Player from '../components/pages/player.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/index' },
  {
    path: '/index',
    component: Index,
    redirect: '/home',
    children: [
      { path: '/home', component: Home },
      { path: '/songlist', component: Songsheet },
      { path: '/singer', component: Singer },
      { path: '/myvip', component: Myvip },
      // { path: '/songbox', component: Songbox },
      { path: '/downloadapp', component: Myapp }
    ]
  },
  { path: '/player', component: Player }

]

const router = new VueRouter({
    mode: 'history',
    routes
  })
  // 登录拦截
