<template>
  <div class="singerpage">
    <div class="singerlist">
      <ul class="litter clearfix">
        <li v-for="(i1,index) in litter"
        :key =index
        :class="current_litter_index==index?'currentli':''"
        @click="current_litter(index,$event)">{{i1}}</li>
      </ul>
      <ul class="region clearfix">
        <li v-for="(i2,index) in region"
        :class="current_region_index==index?'currentli':''"
        @click="current_region(index)"
        :key =index>{{i2}}</li>
      </ul>
      <ul class="sexs clearfix">
        <li v-for="(i3,index) in sexs"
        :class="current_sexs_index==index?'currentli':''"
        @click="current_sexs(index)"
        :key =index>{{i3}}</li>
      </ul>
    </div>
    <div class="singer_artlist">
      <div class="artiest">
        <el-row class="art_list">
          <el-col class="art_list_item" :span="4"  v-for="(item,index) in artiest_list.slice(0,12)" :key = index>
          <div class="art_img"><img :src="item.src" alt="aa"></div>
          <span>{{item.name}}</span>
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="art_box">
      <el-row class="art_box_lis">
        <el-col class="art_box_item" :span="4"  v-for="(item,index) in artiest_name.slice(0,36)" :key = index>
        <span>{{item}}</span>
        </el-col>
      </el-row>
    </div>
      <el-pagination
        class="page_fn"
        layout="prev, pager, next"
        :page-size='20'
        :total="500"

      >
      </el-pagination>
      <!-- zhibo -->
    <Online/>
  </div>
</template>
<script>
import Online from '../public/online'
export default {
  components: {
    Online
  },
  data () {
    return {
      list: 'ABCDEFGHIJKLMNOPQRSTUVWXYZ#'.split(''),
      litter: [],
      region: ['全部', '内地', '港台', '欧美', '韩国', '日本', '其他'],
      sexs: ['全部', '男', '女', '组合', '乐队'],
      current_litter_index: 0,
      current_region_index: 0,
      current_sexs_index: 0,
      artiest_list: [
        { src: require('../../assets/artiest/1.jpg'), name: '胡立' },
        { src: require('../../assets/artiest/2.jpg'), name: '洛天依' },
        { src: require('../../assets/artiest/3.jpg'), name: 'Hitler' },
        { src: require('../../assets/artiest/4.jpg'), name: '何曼婷' },
        { src: require('../../assets/artiest/5.jpg'), name: '周深' },
        { src: require('../../assets/artiest/6.jpg'), name: '徐佳莹' },
        { src: require('../../assets/artiest/6.jpg'), name: '徐佳莹' }

      ],
      artiest_name: ['胡立', '金海心', '戚薇', '阿杜', '李宇春', '金沙', '金海心', '戚薇', '阿杜', '李宇春', '金沙', '胡立', '金海心', '戚薇', '阿杜', '李宇春', '金沙', '胡立', '金海心', '戚薇', '阿杜', '李宇春', '金沙', '胡立', '金海心', '戚薇', '阿杜', '李宇春', '金沙']
    }
  },
  created () {
    window.addEventListener('beforeunload', () => {
      sessionStorage.setItem('current_litter_index', this.current_litter_index)
      sessionStorage.setItem('current_region_index',this.current_region_index)
      sessionStorage.setItem('current_sexs_index', this.current_sexs_index)
      
    })
    this.current_litter_index = window.sessionStorage.getItem('current_litter_index')
    this.current_region_index = window.sessionStorage.getItem('current_region_index')
    this.current_sexs_index = window.sessionStorage.getItem('current_sexs_index')
  },
  beforeDestroy() {
    sessionStorage.setItem('current_litter_index', this.current_litter_index)
    sessionStorage.setItem('current_region_index',this.current_region_index)
    sessionStorage.setItem('current_sexs_index', this.current_sexs_index)
  },
  mounted () {
    this.litter.push(...this.list)
    this.litter.unshift('全部')
    //  console.log(this.litter)
  },
  methods: {
    current_litter (index, e) {
      //  console.log(e.target);
      //  console.log(e.target.innerText)
      this.current_litter_index = index
    },
    current_region (index) {
      this.current_region_index = index
    },
    current_sexs (index) {
      this.current_sexs_index = index
    }
  }
}
</script>
<style lang="scss">
@import "@/assets/css/singer.scss";

</style>
