<template>
  <div class="songsheet">
    <div class="songlist clearfix">
      <div class="list_item" v-for= "item in shitlist" :key = item.id>
        <header>
          <i :class="item.icon" class="iconstyle" ></i>{{item.title}}
        </header>
        <div class="list_content">
          <ul>
            <li v-for= "(i2,index) in item.list" :key = index @click= "change_songsheet" :class="[i2== currentname? 'currentname_style':'']">{{i2}}</li>
            <el-popover
              placement="bottom"
              width='350'
              @show='popovershow(item.id)'
              @hide='popoverhide(item.id)'
              trigger="click">
              <!-- 注意观察dom结构，el-popover没在当前组件里 -->

              <!-- class="iconfont aliiconarrow-down-bold" -->
              <ul class="popover_more" >
                <li v-for= "(i3,index) in item.more" :key = index @click= "change_songsheet" :class="[i3== currentname? 'currentname_style':'']">{{i3}}</li>
              </ul>
              <li slot="reference" class="li_more"  :ref="'more'+item.id"  @click="clickmore(item.id,$event)">更多
                <i class=" iconfont aliiconarrow-down-bold" ></i>
              </li>
            </el-popover>
          </ul>
        </div>
      </div>
    </div>
    <!-- -------- -->
    <div class="all_songsheet">
      <h2>{{this.currentname}}</h2>
    </div>
    <!--  -->
    <div class="sheet_list">
